@import '../../styles/variables';

.container {
  display: flex;
  height: 200px;
  background-color: $black;
  margin-top: 20px;
  margin-bottom: 20px;
  .img {
    height: 100%;
    width: 30%;
    background-size: cover !important;
    background-position: center !important;
  }
  .body {
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .content {
      p {
        opacity: 0.5;
      }
      .stack {
        .stack-item {
          margin-right: 15px;
          margin-bottom: 20px;
        }
      }
    }
  }
}